<template>
  <div id="app">
    <div class="login"><button @click="add">增加item</button><button @click="remove">删除item</button></div>
    
    <div>
      <img width="120" height="120" src="https://user-images.githubusercontent.com/6414178/45662644-3a441000-bb35-11e8-8e72-e6ed618a733d.png">
    </div>
    
    <div class="title">
      Vue Metro Tile
    </div>
    <div class="subtitle">
      *click to animate or drag to rearrange
    </div>

    <div class="grid-layout-container margin-right">
      <grid-layout
                  :layout="layout"
                  :col-num="12"
                  :colWidth="BASE_LEN"
                  :row-height="BASE_LEN"
                  :is-draggable="true"
                  :is-resizable="false"
                  :is-mirrored="false"
                  :vertical-compact="true"
                  :margin="[0, 0]"
                  :use-css-transforms="true"
                  @layout-updated="layoutUpdatedEvent"
      >
        <grid-item v-for="(item) in layout"
                   :x="item.x"
                   :y="item.y"
                   :w="item.w"
                   :h="item.h"
                   :i="item.i"
                   :key="item.i"  
                 >
          <div class="container" @mousedown='onmousedown($event)' @mouseup ="onmouseup($event,item)">
            <MetroTile 
                :width="item.w*BASE_LEN-6"
                :height="item.h*BASE_LEN-6"
                :rotateX="item.rotateX"
                :faceStyle="{background:item['background-color']}"  :data-type='item.moduleid'>
             
              <template v-if="item.type == 1">
                <div slot="front">
                   <img :src="item.img" alt="" class="icon">
                    <div class="tile-label" v-if="item.name">
                     {{item.name}}
                    </div>
                </div>
              </template>
              <!-- 日期类型 -->
              <!-- <template v-else-if="item.type == 2">
                <div slot="front">
                    <div class="calendar">
                      <div class="calendar-week-day">
                        {{weekDay}}
                      </div>
                      <div class="calendar-month-day">
                        {{monthDay}}
                      </div>
                    </div>
                </div>
              </template> -->
              <template v-else-if="item.type === 2">
                <div slot="front" data-type='0' :style='{height:"100%",background:item.item[0]["background-color"]}'>
                    <div class="tile-label">
                     {{item.item[0].name}}
                    </div>
                </div>
                <div slot="top" data-type='1' :style='{height:"100%",background:item.item[1]["background-color"]}'>
                    <div class="tile-label">
                       {{item.item[1].name}}
                    </div>
                </div>
                <div slot="bottom" data-type='2' :style='{height:"100%",background:item.item[2]["background-color"]}'>
                     {{item.item[2].name}}
                </div>
              </template>
            </MetroTile>
          </div>
        </grid-item>
      </grid-layout>
    </div>

    <!-- <div class="grid-layout-container">
      <grid-layout
                  :layout="layoutRight"
                  :col-num="6"
                  :colWidth="BASE_LEN"
                  :row-height="BASE_LEN"
                  :is-draggable="true"
                  :is-resizable="false"
                  :is-mirrored="false"
                  :vertical-compact="true"
                  :margin="[0, 0]"
                  :use-css-transforms="true"
                   @layout-updated="layoutRightUpdatedEvent"
                 
      >
        <grid-item v-for="item in layoutRight"
                   :x="item.x"
                   :y="item.y"
                   :w="item.w"
