                   :i="item.i"
                  
                   :key="item.i">
          <div class="container"  @click="clickFn('right',item.i)">
            <MetroTile 
                :width="item.width"
                :height="item.height"
                :length="item.length"
                :rotateX="item.rotateX"
                :faceStyle="item.faceStyle"
                :frontStyle="item.frontStyle"
                :topStyle="item.topStyle"
                :backStyle="item.backStyle"
                :bottomStyle="item.bottomStyle"
                :perspective="item.perspective"
                v-on="item.i === '5' ? {click: redirect} : {}">
             
              <template v-if="item.type == 1">

                <div slot="front">
                  <div class="tile-label" v-if="item.text">
                   {{item.text}}
                  </div>
                  <img :src="item.url" alt="" class="icon">
                </div>
              </template>
              <template v-else-if="item.type ==2">
                <div slot="front">
                  <div class="tile-label">
                    {{item.text}}
                  </div>
                  <img :src="item.url" class="image" />
                </div>
                <div slot="top">
                  <div class="tile-label">
                   {{item.text1}}
                  </div>
                  <img :src="item.url1" class="image" />
                </div>
              </template>
            </MetroTile>
          </div>
        </grid-item>
      </grid-layout>
    </div> -->
  </div>
</template>

<script>


import MetroTile from './components/MetroTile.vue'
import VueGridLayout from 'vue-grid-layout';


const BASE_LEN = 65;
// const MARGIN = data1.MARGIN;
const REPO_URL = 'https://github.com/yuanfux/vue-metro-tile';

export default {
  name: 'app',
  components: {
    MetroTile,
    VueGridLayout,
   
  },
  data: () => ({
   
    BASE_LEN,
    layout:[],
    weekDay: (() => {
      const weekDayMap = {
        0: 'Sunday',
        1: 'Monday',
        2: 'Tuesday',
        3: 'Wednesday',
        4: 'Thursday',
        5: 'Friday',
        6: 'Saturday'
      }
      return weekDayMap[new Date().getDay()];
    })(),
    monthDay: (() => {
      return new Date().getDate();
    })(),
    rotateArray: [90, -180,90],  
    site:null,
    rotateItemIndex:[]
  }),
  
   mounted() {
     this.getData()
    
    
    
  },
  methods: {
    add(){
      this.itemChange(true)
    },
    remove(){
this.itemChange(false)
    },
    itemChange(add){
      // 数据模板
      let itemTemplate = {
          "moduleid": "unit",
          "name": "机构管理",
          "img": "unit.png",
          "icon": "fa fa-home",
          "jsaction": "alert(2)",
          "width": Math.random()<0.5 && 4 || 2,
          "height": 2,
          "background-color": "#228b22"
      };
     
      if(add){
        this.layout = this.axisFormat([...this.layout,itemTemplate]);
      }else{
        let i = ~~(Math.random()*this.layout.length);
        this.layout.splice(i,1)
      }
    
    },
    
    redirect() {
      window.open(REPO_URL, '_blank');
      // window.location.href = REPO_URL;
    },
    onmousedown(e){
      this.site = {
        x:e.pageX,
        y:e.pageY
      };
    },
    onmouseup(e,item){
      let x = this.site.x - e.pageX;
      let y = this.site.y - e.pageY
      // 点击事件时此差值为0,大于10为容错，若不精准可缩小此值
      if(Math.abs(x)+Math.abs(y)>10){
        return false;
      }
      let jsaction = item.jsaction;
      if(item.item){
        let type = e.target.dataset.type;
        jsaction =  item.item[type] && item.item[type].jsaction || ''
      }
      if(jsaction){
        eval(jsaction)
      }
      
    },
    layoutUpdatedEvent(newLayout){
    
      this.saveData(newLayout)
    },
     getData() {
      let layout = localStorage.getItem('layout');
      // 缓存取值
      if(layout){
          this.layout = JSON.parse(layout);
          this.layout.forEach((item,index)=>{
            if(item.item){
              this.rotateItemIndex .push(index)
            }
          })
          this.registerTimer();
          return false;
      }
      // 服务器取值
      this.http.get('https://www.fastmock.site/mock/4cd04d931e195f79858f5993ff4a1c38/metro/api/metro').then(res => {
      //  数据格式化
       let data = this.axisFormat(res.data.rows);
        this.layout = data;
        // 注册旋转定时器
        this.registerTimer()
      }, err => {
            window.console.log("error",err);
      });
    },
    saveData(newLayout){
      localStorage.setItem('layout',JSON.stringify(newLayout));
    },
    axisFormat(arr){
      // 数据格式化
      let row = 12;
      let col = 0,itemCol=0;
      // 数据过滤及属性初始化
      let oldArr = [];
      let newArr = arr.filter((item,index)=>{
        if(!item.i){
          item.i = ~~(Math.random()*10000+10)
        } ;
        
        item.rotateX = 0;
        item.type = item.item && (this.rotateItemIndex.push(index),2)  || 1;
        if(item.x!==undefined){
          oldArr.push(item)
          return false;
        }
        return true
      })
      if(oldArr.length>0){
        let lastItem = oldArr.sort((x,y)=>y.y-x.y).filter(item=>item.y==oldArr[0].y).sort((x,y)=>y.x-x.x)[0];
        console.log(lastItem)
        col = lastItem.y;
        row = 12-lastItem.x-lastItem.width;
        itemCol = lastItem.height;
        window.console.log(row,col,itemCol)
      }
     
      newArr.map(item=>{
        let w = item.width;
        if(w <= row){
          item.height>itemCol && (itemCol = item.height);
          item.x = 12-row;
        }else{
          col+=itemCol;
          item.x = 0;
          row = 12;
        }
        row-=w;
        item.y = col;
        
        item.w = item.width;
        item.h = item.height;
        console.log(item)
        return item;
      })
      return arr;
    },
    setRotateX(layoutItem, rotateIndexName, rotateArray) {
     
      layoutItem.rotateX += rotateArray[this[rotateIndexName] % rotateArray.length];
      this[rotateIndexName] += 1;
    },
    registerTimer(){
      // // 注册定时器
        this.rotateItemIndex.forEach(item=>{
          this['rotateIndex'+item] = 0;
          this['interval'+item] = setInterval(() => {
           
            this.setRotateX(this.layout[item], 'rotateIndex'+item, this.rotateArray);
          }, 3000);
        })
    },
    clearTimer(){
      // 清除定时器
      this.rotateItemIndex.forEach(item=>{
        clearInterval(this['interval'+item])
      })
    },
   
  },
 
  beforeDestroy() {
    this.clearTimer()
  }
}
</script>

<style>
